header{

    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header_container{

    text-align: center;
    height: 100%;
    position: relative;
}

/* Applying CSS for CTA */

.cta{

    margin-top: 2.5rem;  /*to push buttons littlebit down*/
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* Applying CSS for HeaderSocials */

.header_socials{

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after{

    content: "";
    width: 1px;
    height: 2rem;
    background: var(--colour-primary);
}

/* Applying CSS for My image */

.me{

    background: linear-gradient(var(--colour-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1,5rem 1.5rem;
}

.me > img{

    margin-left: .5rem;
}

/* Applying CSS for Scroll Down */

.scroll_down{

    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* Media queries for medium devices(Tablets) */

@media screen and (max-width: 1024px) {

    header{

        height: 60vh;
    }

}

/* Media queries for small devices(Mobiles) */

@media screen and (max-width: 600px) {

    header{

        height: 100vh;
    }

    .header_socials, .scroll_down{

        display: none;
    }

    .me > img{

        margin-top: 2.5rem;
    }
}